
<template>
  <div class="container mt-5">
    <div class="row">
      <div class="col-md-9 mx-auto">
        <div class="card">
          <div class="card-header">Hello</div>
          <div class="card-body">
            <ul class="list-group">
              <li class="list-group-item" v-if="typing">{{typing}}</li>
              <li
                class="list-group-item"
                v-for="(message, i) in messages"
                :key="i"
              >{{message.from}} : {{message.message}}</li>
            </ul>
            <div class="input-group mt-5 mb-3">
              <input
                v-model="message"
                @keyup.enter="handleSendMessage"
                @keyup="typingx"
                type="text"
                class="form-control"
                placeholder="Recipient's username"
                aria-label="Recipient's username"
                aria-describedby="button-addon2"
              />
              <div class="input-group-append">
                <button
                  @click="handleSendMessage"
                  class="btn btn-outline-secondary"
                  type="button"
                  id="button-addon2"
                >Button</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
/* eslint-disable space-before-function-paren */
export default {
  data() {
    return {
      messages: [],
      message: '',
      email: '',
      room: '',
      typing: ''
    }
  },
  methods: {
    handleSendMessage() {
      this.socket.emit('sendMessage', {
        room: this.room,
        from: this.email,
        message: this.message
      })
      this.message = ''
    },
    typingx() {
      this.socket.emit('typing', {
        room: this.room,
        from: this.email,
        message: this.message
      })
    }
  },
  mounted() {
    if (!this.$route.params.email) {
      this.$router.push('/masuk')
    }
    this.email = this.$route.params.email
    this.room = this.$route.params.room
    this.socket.emit('welcomeMessage', { email: this.email, room: this.room })
    this.socket.on('message', (message) => {
      this.messages.push(message)
    })
    this.socket.on('notif', (message) => {
      this.messages.push(message)
    })
    this.socket.on('sendMessage', (data) => {
      this.messages.push(data)
    })
    this.socket.on('typingNotif', (data) => {
      this.typing = data.message
    })
  },
  computed: mapState(['socket'])
}
</script>

